import React, { useState } from "react";
import ReactDOM from "react-dom";
import {AddWeightForm} from "./components/AddWeightForm/AddWeightForm";
import {AddMealForm} from "./components/AddMealForm/AddMealForm"

import "./styles.css";
import 'bootstrap/dist/css/bootstrap.css';
import Instructions from "./Instructions";

import { List } from "./components/List/List";

import { OrderByDate } from "./components/OrderByDate/OrderByDate";

const initialRecords = [{id: 1, type:"meal", desc: "spaghetti", date: Date.now() }, {id: 2, type:"weight", desc: "111", date: Date.now() }]

function App() {
  const [records, setRecords] = useState(initialRecords);
  const [order, setOrder] = useState("newest");

  const onAddMeal = meal => {
    setRecords(records => [...records, {id: records.length + 1, type:"meal", desc: meal, date: Date.now() }]);
  };

  const onAddWeight = weight => {
    setRecords(records => [...records, {id: records.length + 1, type:"weight", desc: weight, date: Date.now() }]);
  };

  const onOrderBy = (order) => {
    setOrder(order);
  }

  return (
    <div className="App">
      <Instructions />
      <div class="container">
        <div class="row">
          <div class="col-3">
            <AddMealForm onAddMeal={onAddMeal} />
          </div>
          <div class="col-3">
            <AddWeightForm onAddWeight={onAddWeight} />
          </div>
          <div class="col-3">
            <OrderByDate onOrderBy={onOrderBy} />
          </div>
        </div>
      </div>



      <List records={records} orderBy={order}/>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
