import React from "react";
import "./Record.css";
import 'font-awesome/css/font-awesome.min.css';
export function Record( record ) {

  const convertToDate = (ms) => {
    const date = new Date(ms);
    const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
    const month = months[date.getMonth()];
    const day = date.getDate();
    const year = date.getFullYear();
    const time = date.toLocaleTimeString('en-US');
    return `${month} ${day}, ${year} at ${time}`;
  }

  const convertToPounds = (weight) => {
    return Math.round(weight * 2.2 * 100)/100;
  }

  return (
          (record.type === 'meal') ?


          <li className='meal'><i class="fa fa-cutlery" aria-hidden="true"></i> {record.desc} on <strong>{convertToDate(record.date)}</strong></li>
          :
          <li className='weight'><i class="fa fa-balance-scale" aria-hidden="true"></i> {record.desc} kg / {convertToPounds(record.desc)} lb on <strong>{convertToDate(record.date)}</strong></li>
  );
}
