import React, { useState } from "react";
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';


export function AddWeightForm({ onAddWeight }) {
  let unit = React.createRef();
  const [weight, setWeight] = useState("");
  const [show, setShow] = useState(false);

  const handleShow = () => {
    setShow(true);
  }

  const handleClose = () => {
    setShow(false);
  }

  function onSubmitForm(e) {
    e.preventDefault();
    console.log(unit.current.value);
    unit.current.value === 'lb' ? onAddWeight(convertToKg(weight)) : onAddWeight(weight);
    setWeight("");
    handleClose();
  }

  function convertToKg(weight) {
    const weightInKg = weight * 0.453592;
    return Math.round(weightInKg*100)/100;
  }

  return (
    <>
        <Button variant="primary" onClick={handleShow} className="btn btn-success">
          Add Weight
        </Button>

        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Add a new weight</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <form onSubmit={onSubmitForm}>
              <input
                type="text"
                className="form-control"
                value={weight}
                onChange={e => setWeight(e.target.value)}
              />
              <select ref={unit} className="form-control">
                <option value="kg">kg</option>
                <option value="lb">lb</option>
              </select>
              <button type="submit" className="btn btn-success">Add weight</button>
            </form>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </>
  );
}
