import React from 'react';

export function OrderByDate(props) {
  let order = React.createRef();

  function handleOnChange(e) {
    props.onOrderBy(order.current.value);
  }

  return (
    <select ref={order} onChange={handleOnChange} className="form-control">
      <option value="newest">Date: newest first</option>
      <option value="oldest">Date: oldest first</option>
    </select>
  );
}
